.section {
    border: 1px dashed gray;
    padding: 5px;
    margin-bottom: 25px;
    background-color: white;
    cursor: move;
    max-width: fit-content;
    /* width: 800px; */
    overflow: auto;
}

/* .section < */

.add-section {
    border: 1px dashed gray;
    border-radius: 10px;
    padding: 25px;
    width: 752px;
    height: 30px;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.icon {
    margin: auto;
    width: 20px;
    height: 20px;
    text-align: center;
    color: gray;
}

.icon-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.add-icon {
    width: 40px;
    height: 40px;
    margin: auto 20px;
}

input {
    border: 0;
    outline: 0;
}

.story-container {
    width: 853px;
    margin: auto;
    padding: 50px 0;
    text-align: center;
}

.story-container > * {
    display: inline-block;
}

.headline {
    /* margin: s50px auto; */
    font-size: 48px;
    /* width: 750px; */
    margin: auto;
    width: 750px;
    text-align: left;
}

.body {
    font-size: 24px;
    width: 750px;
    resize: none;
    margin: auto;
    border: 0;
    outline: 0;
    text-align: left;
}

.image-container {
    width: 750px;
    margin: auto;
    position: relative;
}

.image {
    /* max-width: 1000px; */
    display: block;
    width: 100%;
    height: auto;
}

.circle {
    border-radius: 50%;
    background-color: white;
    width: 50px;
    height: 50px;
    margin: auto;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    cursor: pointer;
}

.circle .icon {
    margin-top: 12px;
}

.upload {
    /* height: 100%; */
    position: absolute;
    top: 0;
    display: block;
    background: rgba(0, 0, 0, 0.2);
    height: 100%;
}